<template>
  <div class="container">
    <div class="header">
      <h1>To-do List</h1>
    </div>
    
    <div class="addTodoArea">
      <form @submit.prevent="addTodo">
      <input placeholder=" New Task" v-model="newTodo" name="newTodo">
      <button><strong>ADD</strong></button>
    </form>
    </div>
    
    <div class="button_nav">
      <button class="mark_btn" @click="mark">Mark</button>
      <button class="unmark_btn" @click="unmark">Unmark</button>
      <button class="delete_btn" @click="del">Delete</button>
    </div>

    <div class="pending_div">
      <div class="title">
        <h3>Pending</h3>
      </div>
      <div>
        <div>
          <h5 class="select_all">Select All</h5>
          <input class="checkbox_1" @click="selectAll" type="checkbox" v-model="allSelected">
        </div>
        <div>
          <ul>
            <li class="todo_item" v-for="todo in todos" v-bind:key="todo.id" v-show="!todo.done"> 
              <input class="checkbox_1" @change="updateAllSelected" type="checkbox" :value="todo.id" v-model="selected">
              <h4 class="todo" :class="{is_complete: todo.done}">{{ todo.todo }}</h4>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <div class="line"></div>

    <div class="completed_div">
      <div class="title">
        <h3>Completed</h3>
      </div>
      <div>
        <div>
          <h5 class="select_all_2">Select All</h5>
          <input class="checkbox_1" @click="selectAllCompleted" type="checkbox" v-model="allSelectedCompleted">
        </div>
        <div>
          <ul>
            <li class="marked_todo_item" v-for="todo in completedTodos" v-bind:key="todo.id" v-show="todo.done">
              <input class="checkbox_1" @change="updateAllSelectedCompleted" type="checkbox" :value="todo.id" v-model="selected">
              <h4 class="todo" :class="{is_complete: todo.done}">{{ todo.todo }}</h4>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <!--
      - select all (button) (for both select all and unselect all)
      - mark all selected as complete (button)
      - delete all selected (button)

      -completed will be moved to the the bottom/ another div which is slightly
        greyed out
      
    !-->
    </div>
</template>

<script>
import { ref } from 'vue';
import { v4 as uuidv4 } from 'uuid';

export default {
  

  setup() {
    const newTodo = ref('');
    const todos = ref([]);
    const allSelected = ref(false);
    const allSelectedCompleted = ref(false);
    const selected = ref([]);
    const completedTodos = ref([]);


    function addTodo() {
      todos.value.push({
        id: uuidv4(),
        complete: false,
        todo: newTodo.value,
      });
      newTodo.value = '';
    }

    

    function selectAll() {
      allSelectedCompleted.value = false;
      allSelected.value = !allSelected.value
      this.selected = [];
      if (this.allSelected) {
        for (let i in this.todos) {
          this.selected.push(this.todos[i].id);
        }
      }
    }

    function selectAllCompleted() {
      allSelected.value = false;
      allSelectedCompleted.value = !allSelectedCompleted.value
      this.selected = [];
      if (this.allSelectedCompleted) {
        for (let i in this.completedTodos) {
          this.selected.push(this.completedTodos[i].id);
        }
      }
    }

    

    function del() {
      for (let i=todos.value.length - 1; i>=0; i--) {
        for (let j=0; j<selected.value.length; j++) {
          if (this.todos[i].id == this.selected[j]) {
            todos.value.splice(i, 1);
            selected.value.splice(j, 1);
          }
        }
      }
      for (let i=completedTodos.value.length - 1; i>=0; i--) {
        for (let j=0; j<selected.value.length; j++) {
          if (this.completedTodos[i].id == this.selected[j]) {
            completedTodos.value.splice(i, 1);
            selected.value.splice(j, 1);
          }
        }
      }
      selected.value = [];
      allSelected.value = false;
      allSelectedCompleted.value = false;
    }

    function mark() {
      for (let i=todos.value.length - 1; i>=0; i--) {
        for (let j=0; j<selected.value.length; j++) {
          if (this.todos[i].id == this.selected[j]) {
            this.todos[i].done = true;
            this.completedTodos.push(this.todos[i]);
            todos.value.splice(i, 1);
            selected.value.splice(j, 1);
          }
        }
      }
      selected.value = [];
      allSelected.value = false;
      allSelectedCompleted.value = false;
    }
    
    function unmark() {
      for (let i=completedTodos.value.length - 1; i>=0; i--) {
        for (let j=0; j<selected.value.length; j++) {
          if (this.completedTodos[i].id == this.selected[j]) {
            this.completedTodos[i].done = false;
            this.todos.push(this.completedTodos[i]);
            completedTodos.value.splice(i, 1);
            selected.value.splice(j, 1);
          }
        }
      }
      selected.value = [];
      allSelectedCompleted.value = false;
      allSelected.value = false;
      
    }

    function updateAllSelected() {
      for (let i=0; i<completedTodos.value.length; i++) {
        for (let j=0; j<selected.value.length; j++) {
          if (this.completedTodos[i].id == this.selected[j]) {
            allSelectedCompleted.value = false;
            this.selected = [];
          }
        }
      }
      if (selected.value.length == todos.value.length) {
        allSelected.value = true;
      }
      else {
        allSelected.value = false;
      }
    }

    function updateAllSelectedCompleted() {
      for (let i=0; i<todos.value.length; i++) {
        for (let j=0; j<selected.value.length; j++) {
          if (this.todos[i].id == this.selected[j]) {
            allSelected.value = false;
            this.selected = [];
          }
        }
      }
      if (selected.value.length == completedTodos.value.length) {
        allSelectedCompleted.value = true;
      }
      else {
        allSelectedCompleted.value = false;
      }
    }


  

    return {
      todos,
      addTodo,
      newTodo,
      del,
      mark,
      unmark,
      allSelected,
      allSelectedCompleted,
      selected,
      selectAll,
      selectAllCompleted,
      updateAllSelected,
      updateAllSelectedCompleted,
      completedTodos,
    }
  }
}
</script>

